import styled, { css } from 'styled-components';
import theme from '../theme';

const commonStyles = css`
  padding: 11px 28px;
  cursor: pointer;
  border-radius: 4px;
  font-size: 14px;
  border: 2px solid;
  margin-right: 10px;
  margin-top: 10px;
  font-family: Arial;
  font-weight: 600;
`;

const baseStyles = css`
  height: 38px;
  display: inline-block;
  text-align: center;
  text-decoration: none;
`;

const variantStyles = {
  styled: css`
    ${baseStyles}
    padding-top: 10px;
    background-color: ${({ type }) => theme?.[type]};
    color: white;
  `,
  outlined: css`
    ${baseStyles}
    height: 40px;
    background-color: ${theme.badgeText};
    color: ${({ type }) => theme?.[type]};
    border: 1px solid ${({ type }) => theme?.[type]};
    &:hover,
    &:active {
      background-color: ${({ type }) => theme?.[type]};
      color: ${theme.badgeText};
    }
  `,
  text: css`
    ${baseStyles}
    color: ${({ type }) => theme?.[type]};
    background-color: transparent;
    border: none;
  `,
  icon: css`
    ${baseStyles}
    height: 40px;
    padding-top: 8px;
    background-color: ${({ type }) => theme?.[type]};
    color: white;
  `,
  icons: css`
    ${baseStyles}
    height: 38px;
    padding-top: 8px;
    background-color: ${theme.badgeText};
    color: ${({ type }) => theme?.[type]};
    border: 1px solid ${({ type }) => theme?.[type]};
    &:hover,
    &:active {
      background-color: ${({ type }) => theme?.[type]};
      color: ${theme.badgeText};
    }
  `,
};

export const StyledButtonComponent = styled.button`
  ${commonStyles}
  ${({ variant = 'styled' }) => variantStyles[variant]}
`;

export const Icon = styled.span`
  font-size: 16px;
  margin-top: 12px;
  margin-right: 10px;
`;
